import React from 'react'
import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { TYPE } from '../../theme'
import { NavLink } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`
interface StakingModalProps {
  isOpen: boolean
  onDismiss: () => void
}

export default function RecordModal({ isOpen, onDismiss }: StakingModalProps) {
  // monitor call to help UI loading state

  const { t } = useTranslation()
  function wrappedOnDismiss() {
    onDismiss()
  }

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      <ContentWrapper gap="lg" style={{ padding: '20px 0' }}>
        <NavLink
          to="/settings"
          style={{
            textDecoration: 'none',
            borderBottom: '1px solid rgba(237,237,237,0.4)',
            paddingBottom: '16px',
            textAlign: 'center'
          }}
        >
          <TYPE.body style={{ fontWeight: 500 }}>{t('setting')}</TYPE.body>
        </NavLink>
        <NavLink
          to="/record"
          style={{
            textDecoration: 'none',
            borderBottom: '5px solid #ededed',
            paddingBottom: '16px',
            textAlign: 'center'
          }}
        >
          <TYPE.body style={{ fontWeight: 500 }}>{t('record')}</TYPE.body>
        </NavLink>
        <TYPE.body style={{ fontWeight: 500, textAlign: 'center' }} onClick={onDismiss}>
          {t('cancel')}
        </TYPE.body>
      </ContentWrapper>
    </Modal>
  )
}
